.birthday-card {
  position: relative;
  width: 600px; 
  height: 400px;
  /* background-color: lightgoldenrodyellow; */
  border-radius: 14px;
  padding: 50px;
  box-sizing: border-box;
  margin: 0;
  overflow: visible;
}

.cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Maintain aspect ratio and fill the container */
  transition: opacity 0.5s; /* Add transition effect */
}

.inside-page {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  object-fit: cover; /* Maintain aspect ratio and fill the container */
  transition: opacity 0.5s; /* Add transition effect */
}